html {
  font-size: 62.5%;
  box-sizing: border-box;
  background: #111111;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000000;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  width: 100%;
}

.header {
  background-color: #f53756;
}
@media (min-width: 768px) {
  .header {
    left: 0;
    right: 0;
    position: fixed;
    z-index: 1;
  }
}
.header h1 {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
}
.header .contenido_header {
  padding: 1rem;
}
@media (min-width: 768px) {
  .header .contenido_header {
    display: flex;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .navegacion_principal {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}
.navegacion_principal a {
  display: block;
  text-align: center;
  color: #FFFFFF;
  font-size: 2rem;
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .navegacion_principal a {
    margin: 0;
  }
}
.navegacion_principal a:hover {
  color: #FEF45D;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 30rem;
  overflow: hidden;
}
.video .overlay {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: rgb(131, 48, 199);
  background: linear-gradient(90deg, rgba(131, 48, 199, 0.3547794118) 13%, rgba(52, 0, 97, 0.3547794118) 53%, rgba(119, 43, 187, 0.3547794118) 100%);
  position: absolute;
}
.video .overlay .contenedor_video h2 {
  color: white;
  margin-bottom: 1rem;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .video .overlay .contenedor_video h2 {
    font-size: 6rem;
  }
}
.video .overlay .contenedor_video p {
  font-size: 2rem;
  background: #f53756;
  color: white;
  display: inline-block;
  padding: 0.5rem;
  border-radius: 0.5rem;
  margin: 0;
  font-weight: 700;
}
@media (min-width: 768px) {
  .video .overlay .contenedor_video p {
    font-size: 2.5rem;
  }
}
.video video {
  width: auto;
  display: block;
  top: 0rem;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.sobre_festival {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  margin: 8rem auto;
  gap: 2rem;
}
@media (min-width: 992px) {
  .sobre_festival {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(1, 1fr);
    max-width: 120.8rem;
  }
}
.sobre_festival .sobre_festival--img {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.sobre_festival .sobre_festival--img img {
  width: 100%;
}
.sobre_festival .sobre_festival--texto {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.sobre_festival .sobre_festival--texto h2 {
  margin-bottom: 2rem;
  font-size: 5rem;
  color: white;
}
.sobre_festival .sobre_festival--texto h3 {
  margin-bottom: 2rem;
  font-size: 3rem;
  color: #f53756;
}
.sobre_festival .sobre_festival--texto p {
  text-align: justify;
  margin: 0;
  color: white;
  line-height: 3rem;
}

.lineup {
  padding: 5rem 0;
  background: #f53756;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
}
.lineup .dia {
  margin: 8rem 0;
  text-align: center;
  text-transform: uppercase;
}
.lineup .escenarios_contenedor {
  max-width: 120rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .lineup .escenarios_contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.escenario {
  padding: 2rem 0 0 0;
}
.escenario p {
  color: #000000;
}
.escenario .nombre_escenario:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto;
}

.calendario {
  list-style: none;
  margin: 0;
  padding: 0;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-verde {
  background: #4cb8b3;
}
.bg-verde li:nth-child(2n+1) {
  background-color: #3b9691;
}

.bg-amarillo {
  background: #FEF45D;
}
.bg-amarillo li:nth-child(2n+1) {
  background-color: #f3e402;
}

.EM .nombre_escenario:after {
  background-image: url(../img/icono_edm.png);
}

.Rap .nombre_escenario:after {
  background-image: url(../img/icono_rock.png);
}

.galeria {
  max-width: 120rem;
  margin: 0 auto;
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
  color: #FFFFFF;
}

.galeria_img {
  display: grid;
  padding: 0;
  margin: 0 auto;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria_img {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria_img {
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria_img {
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  background: rgba(0, 0, 0, 0.5);
  display: grid;
  overflow: hidden;
  place-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.overlay picture {
  grid-row: 1/2;
  display: block;
  max-width: 80rem;
}

.boton {
  font-size: 3rem;
  display: grid;
  place-items: center;
  background: white;
  width: 5rem;
  height: 5rem;
  border-radius: 3rem;
  font-weight: 700;
}
.boton:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem 0 10rem 0;
  background-color: #f53756;
}
.boletos h3 {
  color: #FFFFFF;
  text-align: center;
}

.contenedor_boletos {
  max-width: 120rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor_boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.contenedor_boletos .basico ul {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
.contenedor_boletos .basico .nombre_pase {
  font-size: 3rem;
  color: #FFFFFF;
  text-align: center;
}
.contenedor_boletos .vip ul {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
.contenedor_boletos .vip .nombre_pase {
  font-size: 3rem;
  color: #FFFFFF;
  text-align: center;
}

.pase_header {
  background: #4cb8b3;
  padding: 0.5rem;
}
.pase_header p {
  text-transform: uppercase;
  margin: 0;
  font-size: 2rem;
  color: #FFFFFF;
  text-align: center;
}

.boletos_lista {
  background: #FFFFFF;
}
.boletos_lista li {
  margin: 0;
  padding: 0.5rem 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.25);
  font-weight: 500;
}

.pase_footer {
  background: #752F97;
  padding: 0.5rem;
}
.pase_footer p {
  text-transform: uppercase;
  margin: 0;
  font-size: 2.6rem;
  color: #FFFFFF;
  text-align: center;
}

.site_footer {
  padding: 2rem 0;
  background-color: black;
}
.site_footer p {
  margin: 0;
  color: white;
  text-align: center;
}